.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Nav-bar {
  background-color: #b2ebd0;
  height: 7vh;
  display: flex;
  align-items: center;
  font-size: calc(10px + 2vmin);
  overflow: hidden;
}

.Nav-bar h4 {
  font-family: cursive, Georgia, 'Times New Roman', Times, serif;
  padding-left: 20px;
  font-weight: normal;
}

.Nav-bar p {
  float: right;
  padding: 14px 16px;
  font-size: 17px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
